export default {
	props: {
		chapter: {

		}
	},
	data() {
		return {
			reversename: false,
			newer: '',
		};
	},
	created() {
		this.newer = this.dateFormat(this.chapter[0].create_date) + ' ' + this.chapter[0].chapter_name;
	},
	methods: {
		reverse() {
			this.reversename = !this.reversename;
			this.$emit('reversechapter', {});
		},
		dateFormat(timestamp) {
			var time = new Date(timestamp * 1000) //先将时间戳转为Date对象，然后才能使用Date的方法
			var year = time.getFullYear(),
				month = time.getMonth() + 1, //月份是从0开始的
				day = time.getDate();

			var ret = year + '-';
			if (month < 10) {
				ret = ret + '0' + month;
			} else {
				ret = ret + month;
			}
			ret += '-';
			if (day < 10) {
				ret = ret + '0' + day;
			} else {
				ret = ret + day;
			}
			//add0()方法在后面定义
			return ret
		},
		goread(item) {
			uni.setStorageSync('temp_read', JSON.stringify(item));
			uni.navigateTo({
				url: '/pages/readcomic/readcomic'
			})
		}
	}
}
